.home-page{
  font-style: italic;
  color: green;
}



.header{
  font-size: 20px;
  font-weight: bold;
}

body {
  font-family: sans-serif;
}

input {
  border: 1px solid #999;
  padding: 0.5rem;
  width: 300px;
}

.no-suggestions {
  color: #999;
  padding: 0.5rem;
}

.suggestions {
  border: 1px solid #999;
  border-top-width: 0;
  list-style: none;
  margin-top: 0;
  max-height: 143px;
  overflow-y: auto;
  padding-left: 0;
  width: calc(300px + 1rem);
}

.suggestions li {
  padding: 0.5rem;
}

.suggestion-active,
.suggestions li:hover {
  background-color: #008f68;
  color: #fae042;
  cursor: pointer;
  font-weight: 700;
}

.suggestions li:not(:last-of-type) {
  border-bottom: 1px solid #999;
}

body.dark-mode {
  background-color: #111;
  color: #eee;
  a {
    color: #111;
  }
  button {
    background-color: #eee;
    color: #111;
  }
}

body.light-mode {
  background-color: #eee;
  color: #111;
  a {
    color: #111;
  }
  button {
    background-color: #111;
    color: #eee;
  }
}
