body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home-page{
  font-style: italic;
  color: green;
}



.header{
  font-size: 20px;
  font-weight: bold;
}

body {
  font-family: sans-serif;
}

input {
  border: 1px solid #999;
  padding: 0.5rem;
  width: 300px;
}

.no-suggestions {
  color: #999;
  padding: 0.5rem;
}

.suggestions {
  border: 1px solid #999;
  border-top-width: 0;
  list-style: none;
  margin-top: 0;
  max-height: 143px;
  overflow-y: auto;
  padding-left: 0;
  width: calc(300px + 1rem);
}

.suggestions li {
  padding: 0.5rem;
}

.suggestion-active,
.suggestions li:hover {
  background-color: #008f68;
  color: #fae042;
  cursor: pointer;
  font-weight: 700;
}

.suggestions li:not(:last-of-type) {
  border-bottom: 1px solid #999;
}

body.dark-mode {
  background-color: #111;
  color: #eee;
  a {
    color: #111;
  }
  button {
    background-color: #eee;
    color: #111;
  }
}

body.light-mode {
  background-color: #eee;
  color: #111;
  a {
    color: #111;
  }
  button {
    background-color: #111;
    color: #eee;
  }
}


.item {
    width: 400px;
    color: #333;
    background: white;
    border: 3px solid#17a2b8;;
    padding: 10px;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    border-radius: 10px;
    box-shadow: 1px 1px 5px rgba(100, 100, 100, 0.5);
}

.item:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
  
  .item img {
    border-radius: 5px;
    width: 200px;
    box-shadow: 1px 1px 1px rgba(100, 100, 100, 0.9);
  }

  .item img .item img::after {
    content: "No image available.";
    display: block;
    height: 100%;
    width: 100%;
  }
  
  #news {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    grid-gap: 15px;
    justify-items: center;
  }


  .item:nth-child(7n+1) {
    border-color: #4285F4;
  }
  
  .item:nth-child(7n+2) {
    border-color: #DB4437;
  }
  
  .item:nth-child(7n+3) {
    border-color: #F4B400;
  }
  
  .item:nth-child(7n+4) {
    border-color: #0F9D58; 
  }
#align-center {
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-content: center;
            align-content: center;
}
